const Dimensions = require('react-native').Dimensions;
const Linking = require('react-native').Linking;
const createHistory = require("history").createMemoryHistory;
const BigNumber = require('bignumber.js');
const copy = require('copy-to-clipboard');
const history = createHistory();

module.exports.copyToClipboard = copyToClipboard;
module.exports.history = history;
module.exports.bn = bn;
module.exports.delay = delay;
module.exports.alert = alert;
module.exports.restartApp = restartApp;
module.exports.openURL = openURL;
module.exports.getScreenDimension = getScreenDimension;

function getScreenDimension() {
  const dim = Dimensions.get('window');
  const isPortrait = (dim.width < dim.height);
  return {
    isPortrait:isPortrait,
    width:dim.width,
    height:dim.height
  };
}

function openURL(url) {
  Linking.canOpenURL(url).then(supported => {
    if (supported) {
      Linking.openURL(url);
    }
  });
}

function restartApp() {
  window.location.reload();
}

function copyToClipboard(msg) {
  return copy(msg);
}

function bn(input) {
  const ret = new BigNumber(input);
  return ret;
}

function delay(ms) {
  return new Promise((resolve, reject) => {
    setTimeout(()=>{
      resolve();
    },ms);
  });
}

function alert(txt) {
  window.alert(txt);
}



