import React from 'react';
import { View, Text, ActivityIndicator, Button, TouchableOpacity } from 'react-native';
import Space from './space';

// import { connect } from 'react-redux';
// import { bindActionCreators } from 'redux';
// import * as actions from './actions/AppActions';

const lib = require('./lib');

class MyPage extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      show:'busy'
    };
  }

  joinTelegram() {
    const url = 'https://t.me/joinchat/Iu0XMRDWN_czSK0pVupkLw';
    lib.openURL(url);
  }

  downloadWhitepaper() {
    lib.openURL('nagatoken-wp.pdf');
  }

  openPreviousProject() {
    const url = 'https://ethdevid.github.io';
    lib.openURL(url);
  }

  componentDidMount() {
    Promise.resolve()
    .then(()=>{
      this.setState({
        show:'content'
      });
    })
  }

  render() {
    const dim = lib.getScreenDimension();

    if(dim.isPortrait) {
      return (
        <View style={{flex:1,backgroundColor:'gainsboro',padding:10}}>
          {this.renderContent()}
        </View>
      )
    }

    const w = dim.height;
    return (
      <View style={{flex:1,flexDirection:'row',justifyContent:'center',backgroundColor:'gainsboro'}}>
        <View style={{width:w,backgroundColor:'gainsboro',padding:10}}>
          {this.renderContent()}
        </View>
      </View>
    )
  }

  renderContent() {
    if(this.state.show === 'busy') {
      return (
        <View style={{alignItems:'center',justifyContent:'center'}}>
          <ActivityIndicator />
        </View>
      );
    }

    return (
      <View style={{flex:1,backgroundColor:'white',borderWidth:1,borderColor:'gray'}}>
        <View style={{flex:1,alignItems:'center',justifyContent:'center'}}>
          <Text style={{textAlign:'center',fontWeight:'bold'}}>KOMUNITAS TOKEN NAGA</Text>
          <Text style={{textAlign:'center'}}>(NAGATOKEN)</Text>
          <Text> </Text>
          <Text style={{textAlign:'center',fontSize:10}}>BERSAMA-SAMA MENJADI TOP 10% YANG MENGUASAI 90% EKONOMI ETHEREUM</Text>
        </View>
        <View style={{paddingHorizontal:10}}>
          <Space />
          <Button title='WHITEPAPER' onPress={()=>this.downloadWhitepaper()} />
          <Space />
          <Button title='TELEGRAM GROUP' onPress={()=>this.joinTelegram()} />
          <Space />
          <Space />
          <TouchableOpacity onPress={()=>this.openPreviousProject()}>
          <Text style={{textAlign:'center',fontSize:10,color:'gray'}}>CLICK UNTUK MEMBUKA PROJECT KAMI LAINNYA: PLATFORMKITA</Text>          
          <Space />
          </TouchableOpacity>
        </View>
      </View>
    )
  }

}

